import './Gallery.css'
import { useState, useEffect, useRef } from 'react';
import { data } from '../../constants';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";



function Arrow(props) {
    let className = props.type === "next" ? "nextArrow" : "prevArrow";
    className += " arrow ";
    return (
      <button className={className} onClick={props.onClick} >
        <svg width="13" height="17" viewBox="0 0 8 14" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M1 0.999999L7 7L1 13" stroke="var(--color-accent)" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
        </svg>
      </button>
    );
  }

function AsNavFor() {
    const [nav1, setNav1] = useState(null);
    const [nav2, setNav2] = useState(null);
    let sliderRef1 = useRef(null);
    let sliderRef2 = useRef(null);
    useEffect(() => {
        setNav1(sliderRef1);
        setNav2(sliderRef2);
    }, []);

    const settings = {
        arrows:  true,
        nextArrow:<Arrow type='next' />,
        prevArrow:<Arrow />}
        
    return (
        <div >
            <Slider
            {...settings}
                asNavFor={nav2}
                ref={slider => (sliderRef1 = slider)}
            >
                {data.placeholderSliderImg_2.map((card, index) => {
                    return (
                        <img key={index} className='gallery_swiper-slide' src={card} alt="car" />
                    )
                })}
            </Slider>
                <Slider
                    asNavFor={nav1}
                    ref={slider => (sliderRef2 = slider)}
                    slidesToShow={5}
                    swipeToSlide={true}
                    focusOnSelect={true}
                >
                    {data.placeholderSliderImg_2.map((card, index) => {
                        return (
                            <img key={index} className='gallery_swiper-slide-small' src={card} alt="car" />
                        )
                    })}
                </Slider>
        </div>
    );
} 

const Gallery = () => (
    <div className='section__padding-noY'>
        <h2 className='colorAccent textCenter underline' style={{paddingBottom:'1rem'}}></h2>
        <AsNavFor />
        </div>
)

export default Gallery