import { Outlet } from 'react-router-dom';
import {ScrollToTop} from '../../components';
import { Navbar, ScrollTop, Footer, ModalPopUp } from '../';

const LayoutApp = () => {
    return (
        <>
            <Navbar />
            <ScrollToTop/>
            <ScrollTop />
            <main>
                <Outlet />
            </main>
            <ModalPopUp/>
            <Footer />
        </>
    )
}

export default LayoutApp