import { useState, useRef } from 'react';
import * as React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { IMaskInput } from 'react-imask';
import { images } from '../../constants';
import './ModalPopUp.css'

const ModalPopUp = () => {
    const ref = useRef(null);
    const inputRef = useRef(null);
    const [userName, setUserName] = useState('');
    const [userPhone, setUserPhone] = useState('');
    const [checked, setChecked] = useState(true);
    const navigate = useNavigate();

    const handlesubmit = (e) => {
        e.preventDefault();
        if (userPhone.length === 11) {
            navigate("/success");
        }

    }


    const [toggleModal, setToggleModal] = useState(true);

    toggleModal ? document.body.classList.add('active-modal') : document.body.classList.remove('active-modal');
    return (
        <>
            {toggleModal &&
                <div className="modalOverlay">
                    <div className='modal_container fade' >
                        <div className='modalOverlay__bg' onClick={()=>{setToggleModal(false)}}></div>
                        <div className='modalOverlay__modal'>
                            <div className='modal' onClick={()=>{setToggleModal(true)}}>
                                <div className='modal_imgContainer' >
                                    <img src={images.modalPopUp} alt="modal" />
                                </div>
                                <div>

                                    <form className='modal_userData' onSubmit={(e) => { handlesubmit(e) }}>
                                        <h2> ?</h2>
                                        <p>  ,    </p>

                                        <div className='modal_userData-container'>
                                            <div className='input-container'>
                                                <input
                                                    id='userName-modal'
                                                    type='text'
                                                    role='textbox'
                                                    placeholder=''
                                                    value={userName}
                                                    required
                                                    onChange={(e) => setUserName(e.target.value)}
                                                />
                                            </div>
                                            <div className='input-container'>
                                                <IMaskInput
                                                    id='userPhone-modal'
                                                    mask={'+{7}(000) 000-00-00'}
                                                    value={userPhone}
                                                    unmask={true}
                                                    ref={ref}
                                                    inputRef={inputRef}
                                                    minlength="17"
                                                    required
                                                    onAccept={(value) => { setUserPhone(value) }}
                                                    placeholder='+7(___)___-__-__'
                                                />
                                            </div>
                                        </div>
                                        <div>

                                            <button>
                                                <div>
                                                    <svg width="19" height="19" viewBox="0 0 19 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M17.0312 12.8959L14.3853 12.5938C14.0742 12.5573 13.7589 12.5917 13.463 12.6946C13.1671 12.7974 12.8984 12.966 12.677 13.1876L10.7603 15.1042C7.80324 13.6003 5.39969 11.1968 3.89575 8.23967L5.82284 6.31258C6.27075 5.86467 6.4895 5.23967 6.41659 4.60425L6.1145 1.97925C6.05545 1.4711 5.81158 1.00241 5.42933 0.662424C5.04709 0.322435 4.55316 0.134892 4.04159 0.1355H2.2395C1.06242 0.1355 0.0832536 1.11467 0.15617 2.29175C0.708254 11.1876 7.82284 18.2917 16.7083 18.8438C17.8853 18.9167 18.8645 17.9376 18.8645 16.7605V14.9584C18.8749 13.9063 18.0833 13.0209 17.0312 12.8959Z" fill="currentColor" />
                                                    </svg>

                                                    <h4> </h4>
                                                </div>
                                            </button>
                                            <div className='input-checkbox'>
                                                <div>
                                                    <input type="checkbox" checked={checked} />
                                                    <span onClick={() => setChecked(!checked)}>
                                                        {checked && <img src={images.checkboxIcon} />}
                                                    </span>
                                                </div>
                                                <p><span onClick={() => setChecked(!checked)} style={{ cursor: 'pointer' }}>         </span><Link to='/politics' className='underline'> </Link></p>
                                            </div>
                                        </div>
                                    </form>

                                </div>
                            </div>

                        </div>
                    </div>
                </div>

            }
        </>


    )
}

export default ModalPopUp