import { data} from "../../constants";
import { SliderType1} from '../../reusable';


const RecomendationsSlider = () => {
    const h2 = <><br /> </>;
    const cardType = 'CardType2';

    return (
        <SliderType1 h2={h2}
                     cards={data.RecomendationsSlider}
                     cardType ={cardType} /> 
    )
}

export default RecomendationsSlider